body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.split {
    height: 100%;
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 0;
    display: flex;
    flex-flow: column;
    transition-duration: 0.4s;
    overflow-x: hidden;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.left,
.right {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.left img,
.right img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    filter: grayscale(1);
    transition-duration: 0.4s;
}

.left img:hover,
.right img:hover,
.ayeb:hover~img,
.fleur:hover~img {
    filter: grayscale(0);
    transform: scale(1.1);
}

.ayeb {
    float: left;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1000;
    text-align: center;
    transform: translate(-50%, -50%);
}

.fleur {
    float: right;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1000;
    text-align: center;
    transform: translate(-50%, -50%);
}

.ayeb h1 {
    font-size: 50px;

}

.ayeb h2 {
    color: white;
    font-weight: bold;
    visibility: hidden;
}

.fleur h1 {
    font-size: 50px;
}

.fleur h2 {
    font-weight: bold;
    color: white;
    visibility: hidden;

}

.right:hover .ayeb h1 {
    color: rgb(255, 0, 85);
}

.left:hover .fleur h1 {
    color: rgb(133, 73, 24);
}

.left:hover .fleur h2 {
    color: rgb(0, 162, 255);
    visibility: visible;
}

.right:hover .ayeb h2 {
    visibility: visible;
}

.left button {
    visibility: hidden;
    background-color: #00a2ff;
    border: 2px solid rgb(133, 73, 24);
    color: white;
    border-radius: 3px;
    width: 100px;
    height: 40px;
    font-weight: bold;
    transition: transform 0.4s;
}

.right button {
    visibility: hidden;
    background-color: rgb(255, 0, 85);
    border: 2px solid white;
    color: white;
    border-radius: 3px;
    width: 100px;
    height: 40px;
    font-weight: bold;
    transition: transform 0.4s;
}

.left:hover button {
    visibility: visible;
}

.right:hover button {
    visibility: visible;
}

#btn_right:hover,
#btn_left:hover {
    cursor: pointer;
    transform: scale(1.2);
}

.left:hover {
    width: 55%;
}

.left:hover~.right {
    width: 45%;
}

.right:hover {
    width: 55%;
}

.right:hover~.left {
    width: 45%;
}

[id^="progress"] {
    margin-top: 40px;
}

#progressleft {
    border: 2px solid rgb(133, 73, 24);
    border-radius: 5px;
    height: 30px;
    color: rgb(133, 73, 24);
    font-weight: bold;
    visibility: hidden;
}

#barleft {
    width: 0%;
    height: 100%;
    line-height: 30px;
    background-color: rgb(0, 162, 255);
    border-radius: 2px;
}

#barright {
    width: 0%;
    height: 100%;
    line-height: 30px;
    background-color: white;
    border-radius: 2px;
}

#progressright {
    border: 2px solid rgb(255, 0, 85);
    border-radius: 5px;
    height: 30px;
    color: rgb(255, 0, 85);
    font-weight: bold;
    visibility: hidden;
}